import React,{Component} from 'react'
import {withSmartOfficeApi} from "../../hoc";
import {connect} from 'react-redux'
import {withRouter} from 'react-router-dom'

import iconGood from '../../../image/status/Normal Face.png'
import AlertIcon from '../../../image/status/Tired Face.png'
import DangerIcon from '../../../image/status/Exhaustion Face.png'

class  Status extends Component{

    state={
        status:''
    };

    componentDidMount() {
        const {services,match} = this.props;
        console.log(this.props);
        services.getApiRoomsStatus(match.params.id).then(el =>{
            this.setState({
                status: el.data
            })
        })
    }
    iconStatus = (status) =>{
       switch (status) {
           case 'good':
               return iconGood;
           case 'alert':
               return  AlertIcon;
           case 'danger':
               return DangerIcon;

           default: return iconGood

       }
    };

    render() {
        const {status} = this.state;
        return(
            <div className='status'>
                <img src={this.iconStatus(status.status)} alt="face"/>
                <div className="status_title">{status.title}</div>
                <div className="status_text">{status.subTitle}</div>
            </div>
        )
    }
};

const mapStateToProps = (state) =>{
    return state
};
const mapDispatchToProps ={

}

export default connect()(withSmartOfficeApi()(withRouter(Status)))